@-webkit-keyframes hideMe {
  0% {
    opacity: 1;
  }

  25% {
    opacity: 1;
  }

  75% {
    opacity: 0;
  }

  85% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}



@keyframes hideMe {
  0% {
    opacity: 1;
  }

  25% {
    opacity: 1;
  }

  75% {
    opacity: 0;
  }

  85% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}


@-webkit-keyframes showMe {
  0% {
    opacity: 0;
  }

  25% {
    opacity: 0;
  }

  50%{
    opacity:0;
  }

  65%{
    opacity:0.8;
  }

  75% {
    opacity: 1;
  }

  100% {
    opacity: 1;
  }
}


@keyframes showMe {
  0% {
    opacity: 0;
  }

  25% {
    opacity: 0;
  }

  50%{
    opacity:0;
  }

  65%{
    opacity:0.8;
  }

  75% {
    opacity: 1;
  }

  100% {
    opacity: 1;
  }
}

